import { memo, type ReactNode } from 'react'

import { useNavigate } from 'react-router-dom'

import { Box, Button } from '@mui/material'

import { routes } from '@/routes'

import { useGithubCardContextNext } from '../hooks/useFlashCardContent'
import type { ContentIdentifier, ContentItem } from '../types'

import ContentStatus from './ContentStatus'

export type ContentNavigationBarProps = {
  contentId: ContentIdentifier
  content?: ContentItem
  contentPrev?: ContentItem
  contentNext?: ContentItem
  centerButton?: ReactNode
}

function ContentNavigationBar({
  contentId,
  content,
  contentPrev,
  contentNext,
  centerButton,
}: ContentNavigationBarProps) {
  const navigate = useNavigate()

  return (
    <Box
      sx={({ palette }) => ({
        position: 'sticky',
        bottom: 0,
        display: 'flex',
        gap: 1,
        mt: 2,
        pt: 2,
        borderTop: 1,
        borderColor: 'divider',
        backgroundColor: palette.background.paper,
        justifyContent: 'space-evenly',
      })}
    >
      <Button
        variant="contained"
        size="large"
        onClick={() => {
          navigate(
            routes.flashMan.learn.$({
              owner: contentId.owner,
              repo: contentId.repo,
              path: contentPrev?.path || '',
            })
          )
        }}
        disabled={!contentPrev}
      >
        {contentPrev && content && <ContentStatus simple content={content} />}
      </Button>
      {centerButton}
      <Button
        variant="contained"
        size="large"
        onClick={() => {
          navigate(
            routes.flashMan.learn.$({
              owner: contentId.owner,
              repo: contentId.repo,
              path: contentNext?.path || '',
            })
          )
        }}
        disabled={!contentNext}
      >
        {contentNext && <ContentStatus simple content={contentNext} />}
      </Button>
    </Box>
  )
}

export default memo(ContentNavigationBar)

/**
 * Connected version that fetches prev/next content via useGithubCardContextNext.
 */
export type ConnectedContentNavigationBarProps = {
  contentId: ContentIdentifier
  centerButton?: ReactNode
}

function ConnectedContentNavigationBarInner({
  contentId,
  centerButton,
}: ConnectedContentNavigationBarProps) {
  const { content, contentPrev, contentNext } = useGithubCardContextNext(contentId)

  return (
    <ContentNavigationBar
      contentId={contentId}
      content={content}
      contentPrev={contentPrev}
      contentNext={contentNext}
      centerButton={centerButton}
    />
  )
}

export const ConnectedContentNavigationBar = memo(ConnectedContentNavigationBarInner)
