import * as React from 'react'

import { intervalToDuration } from 'date-fns/intervalToDuration'
import type { Duration } from 'node_modules/date-fns/types'

import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'

import { useLearningHistorySummary } from '../hooks/useLearningHistorySummary'
import type { ContentItem } from '../types'

import CircularProgressWithLabel from './CircularProgressWithLabel'

export type Props = {
  content: ContentItem
  simple?: boolean
}

function formatDuration({ minutes }: Duration) {
  return `${minutes}`
}

function ContentStatus(props: Props) {
  const { content, simple } = props
  const { sha, type } = content

  const { passRate, duration, cntTries } = useLearningHistorySummary(sha)
  // console.log({ cntTotal });
  return (
    <>
      {type === 'file' && cntTries ? (
        simple ? (
          <>
            <Typography component={'span'} fontSize={12}>
              ({cntTries})
            </Typography>
          </>
        ) : (
          <>
            <Box sx={{ flexGrow: 1 }} />
            <Box>
              <Typography fontSize={12} sx={{ marginRight: '.2rem' }}>
                {formatDuration(intervalToDuration({ start: 0, end: duration }))}
              </Typography>
              <Typography fontSize={12} sx={{ marginRight: '.2rem' }}>
                {cntTries} 
              </Typography>
            </Box>
            <CircularProgressWithLabel value={passRate} />
          </>
        )
      ) : (
        <Typography></Typography>
      )}
    </>
  )
}

export default React.memo(ContentStatus)
