import { memo, Suspense, useEffect, useMemo } from 'react'

import { useAtomValue } from 'jotai'

import { Box, Typography } from '@mui/material'

import { flashManConfigAtom } from '../atoms'
import { useActiveContent, useContentsEx } from '../hooks'
import type { ContentIdentifier, FilterType } from '../types'

import ContentBreadcrumbs from './ContentBreadcrumbs'
import ContentLabel from './ContentLabel'

export type ContentListProps = ContentIdentifier & {
  searchQuery?: string
  filterType?: FilterType
}

function ContentList(props: ContentListProps) {
  const { owner, repo, path = '', searchQuery = '', filterType = 'all' } = props
  const contents = useContentsEx({ owner, repo, path })
  const { setActiveContent } = useActiveContent({ owner, repo, path })
  const { sortByDateDesc } = useAtomValue(flashManConfigAtom)

  const items = useMemo(() => {
    const items = contents instanceof Array ? contents.slice(0) : [contents]
    if (sortByDateDesc) {
      items.sort((a, b) => b.lastModified?.localeCompare(a.lastModified || '') || 0)
    }
    return items
  }, [contents, sortByDateDesc])

  // Apply search filter
  const filteredItems = useMemo(() => {
    if (!searchQuery) return items
    return items.filter((item) => item.name.toLowerCase().includes(searchQuery.toLowerCase()))
  }, [items, searchQuery])

  // Apply status filter (simplified - without learning history tracking)
  const finalItems = useMemo(() => {
    if (filterType === 'all') return filteredItems
    // For now, show all items when filter is not 'all'
    // TODO: implement learning history tracking to enable proper filtering
    return filteredItems
  }, [filteredItems, filterType])

  useEffect(() => {
    if (!(contents instanceof Array)) {
      const item = contents
      setActiveContent(item)
    }
  }, [contents, setActiveContent])

  return (
    <Box>
      <ContentBreadcrumbs owner={owner} repo={repo} path={path} />
      <Suspense fallback={<></>}>
        {finalItems instanceof Array && finalItems.length > 0 ? (
          finalItems.map((content) => (
            <ContentLabel
              key={content.sha}
              content={content}
              owner={owner}
              repo={repo}
              onClick={setActiveContent}
            />
          ))
        ) : (
          <Box sx={{ padding: 4, textAlign: 'center' }}>
            <Typography variant="body2" color="text.secondary">
              {searchQuery ? '  .' : ' .'}
            </Typography>
          </Box>
        )}
      </Suspense>
    </Box>
  )
}

export default memo(ContentList)
