import { memo, useMemo } from 'react'

import { useAtom } from 'jotai'
import { useNavigate } from 'react-router-dom'

import ArticleIcon from '@mui/icons-material/Article'
import FolderOpenIcon from '@mui/icons-material/FolderOpen'
import HomeIcon from '@mui/icons-material/Home'
import SortIcon from '@mui/icons-material/Sort'
import SortByAlphaIcon from '@mui/icons-material/SortByAlpha'
import { Box, Breadcrumbs, Button, IconButton, Typography } from '@mui/material'

import { routes } from '@/routes'

import { flashManConfigAtom } from '../atoms'
import type { ContentIdentifier } from '../types'

export type ContentBreadcrumbsProps = Partial<ContentIdentifier> & {
  file?: boolean
}

type BreadcrumbsItem = {
  name: string
  path: string
}

function ContentBreadcrumbs(props: ContentBreadcrumbsProps) {
  const { owner, repo, path, file } = props
  const navigate = useNavigate()
  const [config, setConfig] = useAtom(flashManConfigAtom)

  const { breadcrumbsItems, lastPath } = useMemo(() => {
    const pathParts = path?.replace(/^\//, '').split('/') || []
    const lastPath = pathParts.pop()
    const breadcrumbsItems = pathParts.reduce<BreadcrumbsItem[]>((ret, part) => {
      const lastPaths = ret[ret.length - 1]?.path
      ret.push({
        name: part,
        path: lastPaths ? lastPaths + '/' + part : part,
      })
      return ret
    }, [])
    return { breadcrumbsItems, lastPath }
  }, [path])

  const handleSortToggle = () => {
    setConfig((prev) => ({ ...prev, sortByDateDesc: !prev.sortByDateDesc }))
  }

  return (
    <Box
      className="ContentBreadcrumbs"
      sx={{ display: 'flex', marginLeft: '10px', marginRight: '10px' }}
    >
      <Breadcrumbs>
        <IconButton
          color="primary"
          onClick={() => navigate(routes.flashMan.contents.$({ owner, repo }))}
        >
          <HomeIcon />
        </IconButton>
        {breadcrumbsItems.map(({ name, path: itemPath }, i) => (
          <Button
            key={i}
            onClick={() => navigate(routes.flashMan.contents.$({ owner, repo, path: itemPath }))}
            sx={{ display: 'flex' }}
          >
            <FolderOpenIcon sx={{ marginRight: '.2rem' }} />
            {name}
          </Button>
        ))}
        {lastPath && (
          <Typography color="text.secondary" sx={{ display: 'flex' }}>
            {file ? (
              <ArticleIcon sx={{ marginRight: '.2rem' }} />
            ) : (
              <FolderOpenIcon sx={{ marginRight: '.2rem' }} />
            )}
            {lastPath}
          </Typography>
        )}
      </Breadcrumbs>
      <Box sx={{ flexGrow: 1 }} />
      {!file && (
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
          <IconButton onClick={handleSortToggle}>
            {config.sortByDateDesc ? <SortIcon /> : <SortByAlphaIcon />}
          </IconButton>
        </Box>
      )}
    </Box>
  )
}

export default memo(ContentBreadcrumbs)
