import { memo, Suspense, useCallback } from 'react'

import ArticleIcon from '@mui/icons-material/Article'
import FolderIcon from '@mui/icons-material/Folder'
import { Box, ListItemButton, ListItemText, styled } from '@mui/material'

import type { ContentItem } from '../types'

import ContentStatus from './ContentStatus'

export type ContentLabelProps = {
  className?: string
  content: ContentItem
  owner?: string
  repo?: string
  onClick?: (content: ContentItem) => void
  onFileClick?: (content: ContentItem) => void
}

const StyledCode = styled('code')(() => ({
  fontSize: '.8rem',
}))

function ContentLabel(props: ContentLabelProps) {
  const { className, content, onClick, onFileClick } = props
  const { name, type } = content

  const handleClick = useCallback(() => {
    if (type === 'file') {
      onFileClick?.(content)
    }
    onClick?.(content)
  }, [content, onClick, onFileClick, type])

  return (
    <ListItemButton
      className={className}
      onClick={handleClick}
      sx={{
        margin: '8px 14px',
        padding: 0,
      }}
    >
      {type === 'dir' && <FolderIcon />}
      {type === 'file' && <ArticleIcon />}
      <ListItemText sx={{ marginLeft: '.4rem' }}>
        <Box>
          <StyledCode>{name}</StyledCode>
        </Box>
      </ListItemText>
      <Suspense fallback={<span></span>}>
        <ContentStatus content={content} />
      </Suspense>
    </ListItemButton>
  )
}

export default memo(ContentLabel)
