import { memo, useMemo } from 'react'

import { scaleLinear } from '@visx/scale'
import { bin, extent } from 'd3-array'

import Box from '@mui/material/Box'
import Slider from '@mui/material/Slider'
import { useTheme } from '@mui/material/styles'

export type HistogramSliderProps = {
  className?: string
  width: number
  height: number
  samples: number[]
  value: number
  onChange: (value: number) => void
}

function HistogramSlider(props: HistogramSliderProps) {
  const { className, width, height, samples, value, onChange } = props

  const { palette } = useTheme()

  const buckets = useMemo(() => bin().domain([0, 100])(samples), [samples])

  const xScale = useMemo(() => {
    return scaleLinear({
      domain: [0, 100],
      range: [0, width],
    })
  }, [width])

  const yScale = useMemo(() => {
    const domain = extent(buckets, ({ length }) => length) as [number, number]
    return scaleLinear({
      domain,
      range: [height, 0],
      nice: true,
    })
  }, [height, buckets])

  return (
    <div className={className}>
      <svg width={width} height={height}>
        {buckets.map(({ length, x0 = 0, x1 = 0 }, i) => {
          const x = xScale(x0)
          const y = yScale(length)
          return (
            <rect
              key={i}
              x={x}
              y={y}
              width={xScale(x1) - x}
              height={height - y}
              fill={x0 < value ? palette.success.main : palette.grey[800]}
            />
          )
        })}
      </svg>
      <Box sx={{ position: 'relative' }}>
        <Slider
          sx={{
            width: 'calc(100% - .4rem)',
          }}
          step={100 / buckets.length}
          marks
          min={0}
          max={100}
          valueLabelDisplay="auto"
          value={value}
          onChange={(_, v) => typeof v === 'number' && onChange(v)}
        />
      </Box>
    </div>
  )
}

export default memo(HistogramSlider)
