import { createContext, memo, useContext, useMemo, type ReactNode } from 'react'

import katex from 'katex'
import markdownIt from 'markdown-it'
import type Token from 'markdown-it/lib/token.mjs'
// @ts-expect-error - no types available for markdown-it-texmath
import markdownItTexMath from 'markdown-it-texmath'

import type { FlashCardContent } from '../../types'

export type RenderContextValue = {
  content: FlashCardContent
  md: markdownIt
  renderMdText: (mdText: string) => Promise<string>
}

const Context = createContext<RenderContextValue>({} as RenderContextValue)

export type RenderContextProviderProps = {
  content: FlashCardContent
  githubToken?: string
  children?: ReactNode
}

async function replaceImageUrls(tokens: Token[], replacer: (url: string) => Promise<string>) {
  const ops = tokens.map(async (token) => {
    if (token.type === 'image') {
      const url = token.attrGet('src')
      if (url) {
        const newUrl = await replacer(url)
        token.attrSet('src', newUrl || url)
      }
    }
    if (token.children) {
      await replaceImageUrls(token.children, replacer)
    }
  })
  return Promise.all(ops)
}

async function mir(md: markdownIt, markdown: string, replacer: (url: string) => Promise<string>) {
  const parsed = md.parse(markdown, { references: {} })
  await replaceImageUrls(parsed, replacer)
  return md.renderer.render(parsed, {}, {})
}

function RenderContextProvider(props: RenderContextProviderProps) {
  const { content, githubToken, children } = props

  const value = useMemo(() => {
    const md = markdownIt({ html: true }).use(markdownItTexMath, {
      engine: katex,
      delimiters: 'dollars',
      katexOptions: { macros: { '\\RR': '\\mathbb{R}' } },
    })

    const { url, name, download_url } = content

    // Safely parse the URL for image resolution
    let baseUrl = ''
    let downloadPath = ''
    try {
      if (url) {
        const urlContent = new URL(url)
        baseUrl = `${urlContent.origin}${urlContent.pathname.replace(new RegExp(`${name}$`), '')}`
      }
      if (download_url) {
        const parts = download_url.split('/')
        parts.pop()
        downloadPath = parts.join('/') + '/'
      }
    } catch {
      // URL parsing failed, leave baseUrl and downloadPath empty
    }

    const renderMdText = async (mdText: string) => {
      const ret =
        (await mir(md, mdText, async (imageUrl: string) => {
          if (imageUrl.startsWith('http')) {
            return imageUrl
          }
          const rawGitUrl = downloadPath + imageUrl
          if (!baseUrl) {
            return rawGitUrl
          }
          try {
            const response = await fetch(`${baseUrl}${imageUrl}`, {
              headers: githubToken
                ? {
                    Authorization: `Bearer ${githubToken}`,
                  }
                : {},
            })
            const json = (await response.json()) as { content: string }
            return `data:image/jpeg;base64,${json.content}`
          } catch (e) {
            // eslint-disable-next-line no-console
            console.error(e)
          }
          return rawGitUrl
        })) || ''
      return ret
    }

    return {
      content,
      md,
      renderMdText,
    }
  }, [content, githubToken])

  return <Context.Provider value={value}>{children}</Context.Provider>
}

export default memo(RenderContextProvider)

// eslint-disable-next-line react-refresh/only-export-components
export function useRenderContext() {
  return useContext(Context)
}
