import { Component, memo, Suspense, type ReactNode } from 'react'

import { useNavigate } from 'react-router-dom'

import {
  Box,
  Button,
  CircularProgress,
  LinearProgress,
  Typography,
  type BoxProps,
  mergeSlotProps,
} from '@mui/material'

import { routes } from '@/routes'

import type { ContentIdentifier } from '../types'

import CircularProgressWithLabel from './CircularProgressWithLabel'
import { ConnectedContentNavigationBar } from './ContentNavigationBar'

export type QuizStats = BoxProps & {
  currentIndex: number
  total: number
  passCount: number
  failCount: number
  progress: number
}

export function QuizEmptyState() {
  return (
    <Box sx={{ padding: 4, textAlign: 'center' }}>
      <Typography variant="body2" color="text.secondary">
          .
      </Typography>
    </Box>
  )
}

class SilentErrorBoundary extends Component<{ children: ReactNode }, { hasError: boolean }> {
  state = { hasError: false }
  static getDerivedStateFromError() {
    return { hasError: true }
  }
  render() {
    if (this.state.hasError) return null
    return this.props.children
  }
}

function getAccuracyColor(percent: number): 'success' | 'warning' | 'error' {
  if (percent >= 80) return 'success'
  if (percent >= 50) return 'warning'
  return 'error'
}

export function QuizCompleteState({
  className,
  passCount,
  failCount,
  total,
  contentId,
  onRestart,
}: {
  className?: string
  passCount: number
  failCount: number
  total: number
  contentId: ContentIdentifier
  onRestart: () => void
}) {
  const navigate = useNavigate()
  const accuracy = total > 0 ? Math.round((passCount / total) * 100) : 0
  const progressColor = getAccuracyColor(accuracy)
  return (
    <Box
      className={className}
      sx={{
        height: '100vh',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        padding: 2,
      }}
    >
      <Box sx={{ display: 'flex', gap: 1, alignSelf: 'flex-end' }}>
        <Button
          variant="outlined"
          size="small"
          onClick={() => {
            navigate(routes.flashMan.cards.$(contentId))
          }}
        >
          
        </Button>
        <Button
          variant="outlined"
          size="small"
          onClick={() => {
            navigate(routes.flashMan.learn.$(contentId))
          }}
        >
          
        </Button>
      </Box>
      <Box
        sx={{
          flexGrow: 1,
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
          gap: 2,
        }}
      >
        <CircularProgressWithLabel value={accuracy} size={120} color={progressColor} />
        <Typography variant="h4" fontWeight="bold">
           !
        </Typography>
        <Typography variant="h6">
          <Typography component="span" color="success.main" fontWeight="bold">
             {passCount}
          </Typography>
          {' / '}
          <Typography component="span" color="error.main" fontWeight="bold">
             {failCount}
          </Typography>
        </Typography>
      </Box>
      <SilentErrorBoundary>
        <Suspense>
          <ConnectedContentNavigationBar
            contentId={contentId}
            centerButton={
              <Button variant="contained" size="large" onClick={onRestart}>
                
              </Button>
            }
          />
        </Suspense>
      </SilentErrorBoundary>
    </Box>
  )
}

export function QuizProgressBar({
  currentIndex,
  total,
  passCount,
  failCount,
  progress,
  ...boxProps
}: QuizStats) {
  return (
    <Box {...mergeSlotProps(boxProps, {})}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
        <Typography variant="body2" color="text.secondary">
          {currentIndex + 1} / {total}
        </Typography>
        <Typography variant="body2">
          <Typography component="span" color="success.main">
            {passCount}
          </Typography>
          {' / '}
          <Typography component="span" color="error.main">
            {failCount}
          </Typography>
        </Typography>
      </Box>
      <LinearProgress variant="determinate" value={progress} sx={{ height: 8, borderRadius: 4 }} />
    </Box>
  )
}

function QuizSuspenseWrapperInner({ children }: { children: ReactNode }) {
  return (
    <Suspense
      fallback={
        <Box
          sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '50vh' }}
        >
          <CircularProgress />
        </Box>
      }
    >
      {children}
    </Suspense>
  )
}

export const QuizSuspenseWrapper = memo(QuizSuspenseWrapperInner)
