import { memo, useCallback, useEffect, useMemo, useState } from 'react'

import { useAtomValue } from 'jotai'
import { useNavigate } from 'react-router-dom'

import CloseIcon from '@mui/icons-material/Close'
import HearingIcon from '@mui/icons-material/Hearing'
import { Box, Button, Card, CardContent, IconButton, Typography } from '@mui/material'
import type { ButtonProps } from '@mui/material'

import { useIsMobile } from '@/hooks/useIsMobile'
import { useTypedSearchParams } from '@/hooks/useTypedSearchParams'
import { routes } from '@/routes'

import { learnConfigAtom } from '../atoms'
import { useFlashCardQuiz } from '../hooks/useFlashCardQuiz'
import { useTts } from '../lib/tts'
import type { FlashCardItem } from '../types'
import { shuffleArray } from '../utils'

import {
  QuizCompleteState,
  QuizEmptyState,
  QuizProgressBar,
  QuizSuspenseWrapper,
} from './QuizLayout'
import { Answer, Question, RenderContextProvider } from './RenderContext'

export type OneFromManyViewProps = {
  className?: string
}

type Stage = 'Guessing' | 'Studying' | 'Answering'

type Answer = {
  a: string
  correctAnswer?: boolean
}

function makeAnswers(flashCardItems: FlashCardItem[], questionIndex: number): Answer[] {
  const item = flashCardItems[questionIndex]
  const refinedAnswers = flashCardItems.map(({ a }) => a.trim().toUpperCase())
  const uniqueAnswers = Array.from(new Set(refinedAnswers))

  // If there are 4 or fewer unique answers, use them all
  if (uniqueAnswers.length <= 4) {
    return uniqueAnswers.map((a) => ({
      a: flashCardItems.find((i) => i.a.trim().toUpperCase() === a)?.a || a,
      correctAnswer: item.a.trim().toUpperCase() === a,
    }))
  }

  // Otherwise, pick 3 random wrong answers + the correct one
  const wrongAnswers = shuffleArray(flashCardItems.filter((i) => i.a !== item.a))
    .slice(0, 3)
    .map(({ a }) => ({ a }))

  return shuffleArray([...wrongAnswers, { a: item.a, correctAnswer: true }])
}

function OneFromManyViewContent(props: OneFromManyViewProps) {
  const { className } = props
  const { queryParams } = useTypedSearchParams(routes.flashMan.learn.oneFromMany.$config)
  const { owner, repo, path } = queryParams

  const {
    content,
    token,
    flashCardItems,
    currentIndex,
    currentItem,
    total,
    passCount,
    failCount,
    progress,
    isComplete,
    recordResult,
    advance,
    restart,
  } = useFlashCardQuiz({ owner, repo, path })

  const { isMobile } = useIsMobile()
  const navigate = useNavigate()

  const { ttsForQuestion, ttsForAnswer, ttsLang } = useAtomValue(learnConfigAtom)
  const playTts = useTts(ttsLang)

  const [stage, setStage] = useState<Stage>('Guessing')
  const [selected, setSelected] = useState<number | undefined>()

  const answers = useMemo(() => {
    if (!currentItem || flashCardItems.length === 0) return []
    return makeAnswers(flashCardItems, currentIndex)
  }, [flashCardItems, currentIndex, currentItem])

  // Auto-play TTS when question changes
  useEffect(() => {
    if (!currentItem) return
    const play = async () => {
      if (ttsForQuestion) {
        await playTts(currentItem.q)
      }
      if (ttsForAnswer) {
        for (const { a } of answers) {
          await playTts(a)
        }
      }
    }
    play()
  }, [currentIndex, currentItem, ttsForQuestion, ttsForAnswer, answers, playTts])

  const replay = useCallback(async () => {
    if (!currentItem) return
    await playTts(currentItem.q)
    if (ttsForAnswer) {
      for (const { a } of answers) {
        await playTts(a)
      }
    }
  }, [currentItem, answers, ttsForAnswer, playTts])

  const handleResult = useCallback(
    (result: 'pass' | 'fail') => {
      recordResult(result)
      setStage('Guessing')
      setSelected(undefined)
      advance()
    },
    [recordResult, advance]
  )

  const handleRestart = useCallback(() => {
    restart()
    setStage('Guessing')
    setSelected(undefined)
  }, [restart])

  // Handle answer selection
  useEffect(() => {
    if (selected !== undefined && answers[selected]) {
      const isCorrect = answers[selected].correctAnswer
      const timeout = setTimeout(
        () => {
          handleResult(isCorrect ? 'pass' : 'fail')
        },
        isCorrect ? 1000 : 3000
      )
      return () => clearTimeout(timeout)
    }
  }, [selected, answers, handleResult])

  // Keyboard shortcuts
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (stage === 'Guessing') {
        if (e.key === 's' || e.key === 'S') {
          setStage('Studying')
        } else if (e.key === 'a' || e.key === 'A' || e.key === ' ' || e.key === 'Enter') {
          setStage('Answering')
        }
      } else if (stage === 'Studying') {
        if (e.key === ' ' || e.key === 'Enter') {
          handleResult('fail')
        }
      } else if (stage === 'Answering' && selected === undefined) {
        const num = parseInt(e.key)
        if (num >= 1 && num <= answers.length) {
          setSelected(num - 1)
        }
      }
    }

    window.addEventListener('keydown', handleKeyDown)
    return () => window.removeEventListener('keydown', handleKeyDown)
  }, [stage, selected, answers.length, handleResult])

  if (!content || flashCardItems.length === 0) {
    return <QuizEmptyState />
  }

  if (isComplete) {
    return (
      <QuizCompleteState
        className={className}
        passCount={passCount}
        failCount={failCount}
        total={total}
        contentId={{ owner: owner || '', repo: repo || '', path }}
        onRestart={handleRestart}
      />
    )
  }

  return (
    <RenderContextProvider content={content} githubToken={token}>
      <Box
        className={'OneFromManyView ' + (className || '')}
        // className={className}
        sx={{
          display: 'flex',
          flexDirection: 'column',
          height: '100%',
          padding: 2,
          minHeight: '100vh',
        }}
      >
        <Box sx={{ display: 'flex', mb: 2 }}>
          <Box>
            <IconButton
              sx={{ transform: 'translate(-.5rem, -.2rem)' }}
              onClick={() => {
                navigate(routes.flashMan.learn.$(queryParams))
              }}
            >
              <CloseIcon />
            </IconButton>
          </Box>
          <QuizProgressBar
            sx={{ flexGrow: 1 }}
            currentIndex={currentIndex}
            total={total}
            passCount={passCount}
            failCount={failCount}
            progress={progress}
          />
        </Box>

        {/* Question Card */}
        <Card sx={{ display: 'flex', flexDirection: 'column' }}>
          <CardContent
            sx={{
              flexGrow: 1,
              display: 'flex',
              flexDirection: 'column',
              padding: '1rem !important',
            }}
          >
            {/* Question */}
            <Box sx={{ flexGrow: 1, display: 'flex', py: 2 }}>
              <Question text={currentItem?.q || ''} />
            </Box>

            {/* Answer (only in Studying stage) */}
            {stage === 'Studying' && (
              <Box
                sx={{
                  borderTop: 1,
                  borderColor: 'divider',
                  py: 2,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                }}
              >
                <Answer text={currentItem?.a || ''} sx={{ color: 'primary.main' }} />
              </Box>
            )}
          </CardContent>
        </Card>

        <Box sx={{ flexGrow: 1, minHeight: '1rem' }} />

        {/* Controls based on stage */}
        {stage === 'Guessing' && (
          <Box sx={{ display: 'flex', gap: 1 }}>
            {(ttsForQuestion || ttsForAnswer) && (
              <IconButton onClick={replay} size="large">
                <HearingIcon />
              </IconButton>
            )}
            <Button
              variant="outlined"
              fullWidth
              size="large"
              onClick={() => setStage('Studying')}
              sx={{ fontSize: '1.1rem' }}
            >
               (S)
            </Button>
            <Button
              variant="contained"
              fullWidth
              size="large"
              onClick={() => setStage('Answering')}
              sx={{ fontSize: '1.1rem' }}
            >
               (A)
            </Button>
          </Box>
        )}

        {stage === 'Studying' && (
          <Box sx={{ display: 'flex', gap: 1 }}>
            <Button
              variant="outlined"
              // size="large"
              onClick={() => {
                window.open(
                  `https://translate.google.co.kr/?hl=ko&sl=auto&text=${encodeURIComponent(currentItem?.q || '')}&op=translate`,
                  '_blank'
                )
              }}
              sx={{ fontSize: '1rem' }}
            >
              
            </Button>
            <Button
              variant="outlined"
              // size="large"
              onClick={() => {
                window.open(
                  `https://www.google.com/search?q=${encodeURIComponent(currentItem?.q || '')}`,
                  '_blank'
                )
              }}
              sx={{ fontSize: '1rem' }}
            >
              
            </Button>
            <Button
              variant="contained"
              fullWidth
              // size="large"
              onClick={() => handleResult('fail')}
              sx={{ fontSize: '1.1rem' }}
            >
              {isMobile ? '' : ' (Space)'}
            </Button>
          </Box>
        )}

        {stage === 'Answering' && (
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 1 }}>
            {answers.map((answer, i) => {
              let color: ButtonProps['color'] = 'primary'
              if (selected !== undefined) {
                if (answer.correctAnswer) {
                  color = 'success'
                } else if (selected === i) {
                  color = 'error'
                }
              }
              return (
                <Button
                  key={i}
                  variant="outlined"
                  size="large"
                  color={color}
                  // disabled={selected !== undefined}
                  onClick={() => {
                    if (selected === undefined) {
                      setSelected(i)
                    }
                  }}
                  sx={{
                    justifyContent: 'flex-start',
                    textAlign: 'left',
                    fontSize: '1rem',
                    py: 1.5,
                  }}
                >
                  {/* <Typography component="span" sx={{ mr: 1, fontWeight: 'bold' }}>
                    {i + 1}.
                  </Typography> */}
                  <Answer text={answer.a} />
                </Button>
              )
            })}
          </Box>
        )}

        {/* Keyboard hints */}
        {!isMobile && (
          <Typography variant="caption" color="text.secondary" sx={{ mt: 2, textAlign: 'center' }}>
            {stage === 'Guessing' && 'S:  | A/Space/Enter: '}
            {stage === 'Studying' && 'Space/Enter: '}
            {stage === 'Answering' && '1-4:  '}
          </Typography>
        )}
      </Box>
    </RenderContextProvider>
  )
}

function OneFromManyView(props: OneFromManyViewProps) {
  return (
    <QuizSuspenseWrapper>
      <OneFromManyViewContent {...props} />
    </QuizSuspenseWrapper>
  )
}

export default memo(OneFromManyView)
