import { memo } from 'react'

import { Box, Card, CardContent } from '@mui/material'

import type { FlashCardItemWithSummary } from '../types'

import CircularProgressWithLabel from './CircularProgressWithLabel'
import { Answer, Question } from './RenderContext'

export type FlashCardCardProps = {
  className?: string
  item: FlashCardItemWithSummary
}

function FlashCardCard(props: FlashCardCardProps) {
  const { className, item } = props
  const { q, a, passRatio } = item

  return (
    <Card className={className} elevation={3}>
      <CardContent sx={{ paddingBottom: '16px !important' }}>
        <Box sx={{ display: 'flex', flexDirection: 'row' }}>
          <Box sx={{ flex: 1 }}>
            <Question
              text={q}
              sx={{
                fontSize: '.8rem',
                color: 'text.secondary',
                lineHeight: 'inherit',
              }}
            />
            <Answer
              text={a}
              sx={{
                fontSize: '1rem',
                marginTop: '4px',
              }}
            />
          </Box>
          <Box sx={{ flexGrow: 1 }} />
          <Box>
            <CircularProgressWithLabel value={passRatio} />
          </Box>
        </Box>
      </CardContent>
    </Card>
  )
}

export default memo(FlashCardCard)
