import { memo, Suspense, useState } from 'react'

import { useNavigate } from 'react-router-dom'

import ListIcon from '@mui/icons-material/List'
import SchoolIcon from '@mui/icons-material/School'
import SortIcon from '@mui/icons-material/Sort'
import { Box, Button, CircularProgress, Grid, IconButton, Link, Typography } from '@mui/material'

import { useTypedSearchParams } from '@/hooks/useTypedSearchParams'
import { routes } from '@/routes'

import { useGithubToken } from '../hooks'
import { useFlashCardContent } from '../hooks/useFlashCardContent'
import { useFlashCardItemSummaries } from '../hooks/useFlashCardItemSummaries'

import ContentBreadcrumbs from './ContentBreadcrumbs'
import FlashCardCard from './FlashCardCard'
import { RenderContextProvider } from './RenderContext'

export type FlashCardViewProps = {
  className?: string
}

function FlashCardViewContent(props: FlashCardViewProps) {
  const { className } = props

  const { queryParams } = useTypedSearchParams(routes.flashMan.cards.$config)
  const { owner, repo, path } = queryParams
  const token = useGithubToken()
  const navigate = useNavigate()
  const [sortByPassRatio, setSortByPassRatio] = useState(true)

  const { content } = useFlashCardContent(queryParams)

  const itemsWithSummary = useFlashCardItemSummaries(content ?? null, sortByPassRatio)

  if (!content) {
    return (
      <Box sx={{ padding: 4, textAlign: 'center' }}>
        <Typography variant="body2" color="text.secondary">
            ...
        </Typography>
      </Box>
    )
  }

  return (
    <Box
      className={className}
      sx={{
        display: 'flex',
        flexDirection: 'column',
        flexGrow: 1,
      }}
    >
      <Box sx={{ margin: '1rem', display: 'flex', alignItems: 'center', flexDirection: 'row' }}>
        <ContentBreadcrumbs owner={owner} repo={repo} path={path} file />
        <Box sx={{ flexGrow: 1 }} />
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'flex-end',
          }}
        >
          <Typography component="span" sx={{ marginRight: '.4rem' }}>
            ({itemsWithSummary.length})
          </Typography>
          {content.html_url && (
            <Link href={content.html_url} target="_blank" rel="noopener noreferrer">
              GitHub
            </Link>
          )}
        </Box>
        <IconButton onClick={() => setSortByPassRatio(!sortByPassRatio)}>
          {sortByPassRatio ? <SortIcon /> : <ListIcon />}
        </IconButton>
      </Box>

      {/* Learn Button */}
      {itemsWithSummary.length > 0 && (
        <Box sx={{ padding: '0 1rem 1rem' }}>
          <Button
            variant="contained"
            fullWidth
            size="large"
            startIcon={<SchoolIcon />}
            onClick={() => {
              navigate(routes.flashMan.learn.$({ owner, repo, path }))
            }}
          >
            
          </Button>
        </Box>
      )}

      <RenderContextProvider content={content} githubToken={token}>
        <Grid container spacing={2} sx={{ padding: '0 1rem 1rem' }}>
          {itemsWithSummary.map((item, i) => (
            <Grid key={i} size={12}>
              <FlashCardCard item={item} />
            </Grid>
          ))}
        </Grid>
      </RenderContextProvider>
    </Box>
  )
}

function FlashCardView(props: FlashCardViewProps) {
  return (
    <Suspense
      fallback={
        <Box
          sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '50vh' }}
        >
          <CircularProgress />
        </Box>
      }
    >
      <FlashCardViewContent {...props} />
    </Suspense>
  )
}

export default memo(FlashCardView)
