import { useEffect } from 'react'

export function useStackedTitle(text?: string) {
  useEffect(() => {
    const titlePrev = document.title
    if (text) {
      document.title = titlePrev ? `${text} | ${titlePrev}` : text
    }
    return () => {
      if (text) {
        document.title = titlePrev
      }
    }
  }, [text])
}
