import { memo, Suspense, useEffect, useState } from 'react'

import { useAtomValue } from 'jotai'

import ClearIcon from '@mui/icons-material/Clear'
import SearchIcon from '@mui/icons-material/Search'
import {
  Autocomplete,
  Box,
  Chip,
  CircularProgress,
  IconButton,
  InputAdornment,
  TextField,
  ToggleButton,
  ToggleButtonGroup,
} from '@mui/material'

import { useTypedSearchParams } from '@/hooks/useTypedSearchParams'
import { routes } from '@/routes'

import { DEFAULT_REPO_ID, flashManConfigAtom } from '../atoms'
import { useRepositoryList } from '../hooks'
import type { FilterType } from '../types'

import ContentList from './ContentList'
import ContentTree from './ContentTree'

const LAST_REPO_KEY = 'flash-man:last-repo'

export type ContentViewProps = {
  className?: string
}

function ContentView(props: ContentViewProps) {
  const { className } = props
  const {
    queryParams: { owner, repo, path },
    mergeQueryParams,
  } = useTypedSearchParams(routes.flashMan.contents.$config)

  const config = useAtomValue(flashManConfigAtom)
  const [searchQuery, setSearchQuery] = useState('')
  const [filterType, setFilterType] = useState<FilterType>('all')

  const repos = useRepositoryList(config.repos, searchQuery)

  const handleClearSearch = () => {
    setSearchQuery('')
  }

  const currentRepoValue = `${owner || DEFAULT_REPO_ID.owner}/${repo || DEFAULT_REPO_ID.repo}`

  useEffect(() => {
    if (!owner && !repo && repos.length > 0) {
      // Check localStorage for last selected repo
      const lastRepo = localStorage.getItem(LAST_REPO_KEY)
      if (lastRepo) {
        const [savedOwner, savedRepo] = lastRepo.split('/')
        // Verify the saved repo exists in the list
        const exists = repos.some((r) => r.owner === savedOwner && r.repo === savedRepo)
        if (exists) {
          mergeQueryParams({ owner: savedOwner, repo: savedRepo })
          return
        }
      }
      // Fall back to first repo
      mergeQueryParams(DEFAULT_REPO_ID)
    }
  }, [owner, repo, path, repos, mergeQueryParams])

  // console.log('ContentView', { owner, repo, path, currentRepoValue })
  return (
    <Box className={className}>
      <Box sx={{ margin: '1rem 1rem .4rem', display: 'flex', flexDirection: 'column', gap: 2 }}>
        {/* Repository Selection */}
        <Autocomplete
          fullWidth
          options={repos}
          getOptionLabel={(option) => option.label}
          value={repos.find((r) => r.label === currentRepoValue) || null}
          onChange={(_, newValue) => {
            if (newValue) {
              const { owner: newOwner, repo: newRepo } = newValue
              localStorage.setItem(LAST_REPO_KEY, `${newOwner}/${newRepo}`)
              mergeQueryParams({ owner: newOwner, repo: newRepo })
            }
          }}
          renderInput={(params) => <TextField {...params} label="" />}
        />

        {/* Search Bar */}
        <TextField
          fullWidth
          size="small"
          placeholder=" ..."
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          slotProps={{
            input: {
              startAdornment: (
                <InputAdornment position="start">
                  <SearchIcon />
                </InputAdornment>
              ),
              endAdornment: searchQuery && (
                <InputAdornment position="end">
                  <IconButton size="small" onClick={handleClearSearch}>
                    <ClearIcon />
                  </IconButton>
                </InputAdornment>
              ),
            },
          }}
        />

        {/* Filter */}
        <Box sx={{ display: 'flex', gap: 1, flexWrap: 'wrap', alignItems: 'center' }}>
          <ToggleButtonGroup
            size="small"
            value={filterType}
            exclusive
            onChange={(_, value) => value && setFilterType(value)}
          >
            <ToggleButton value="all"></ToggleButton>
            <ToggleButton value="not-started">  </ToggleButton>
            <ToggleButton value="in-progress"> </ToggleButton>
            <ToggleButton value="completed"></ToggleButton>
          </ToggleButtonGroup>
          {filterType !== 'all' && (
            <Chip label=" " size="small" onDelete={() => setFilterType('all')} />
          )}
        </Box>
      </Box>

      <Suspense
        fallback={
          <Box
            sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '70vh' }}
          >
            <CircularProgress />
          </Box>
        }
      >
        {owner && repo && (
          <>
            {config.viewType === 'tree' ? (
              <ContentTree
                owner={owner}
                repo={repo}
                path={path}
                searchQuery={searchQuery}
                filterType={filterType}
              />
            ) : (
              <ContentList
                owner={owner}
                repo={repo}
                path={path}
                searchQuery={searchQuery}
                filterType={filterType}
              />
            )}
          </>
        )}
      </Suspense>
    </Box>
  )
}

export default memo(ContentView)
