import { useMemo } from 'react'

import { useSuspenseQuery } from '@tanstack/react-query'

import { useContentsEx, useGithubToken, useOctokit } from '../hooks'
import { getFlashCardContent } from '../lib/github'
import type { ContentIdentifier } from '../types'

export function useFlashCardContent({ owner, repo, path }: Partial<ContentIdentifier>) {
  const octokit = useOctokit()
  const token = useGithubToken()
  const { data: content } = useSuspenseQuery({
    queryKey: ['useFlashCardContent', owner, repo, path, token],
    queryFn: async () => {
      if (!owner || !repo || !path) return null
      return getFlashCardContent(octokit, { owner, repo }, path)
    },
    staleTime: 1000 * 60 * 5,
  })
  return { content }
}

export function useGithubCardContextNext(contentIdentifier: ContentIdentifier) {
  const { content } = useFlashCardContent(contentIdentifier)
  const contentIdentifierForParent = useMemo(() => {
    const { owner, repo, path } = contentIdentifier
    const parts = path?.split('/')
    parts?.pop()
    return {
      owner,
      repo,
      path: parts?.join('/'),
    }
  }, [contentIdentifier])
  // console.log('useGithubParentContents', contentIdentifierForParent)
  const contents = useContentsEx(contentIdentifierForParent)
  return useMemo(() => {
    if (content && contents instanceof Array) {
      const findIndex = contents.findIndex(({ name }) => name === content.name)
      const contentPrev = contents[findIndex - 1]
      const contentNext = contents[findIndex + 1]
      return {
        contentPrev,
        content,
        contentNext,
      }
    }
    return {}
  }, [content, contents])
}
