import { memo, Suspense, useMemo } from 'react'

import ChevronRightIcon from '@mui/icons-material/ChevronRight'
import ExpandMoreIcon from '@mui/icons-material/ExpandMore'
import { Box, Typography } from '@mui/material'
import { SimpleTreeView } from '@mui/x-tree-view/SimpleTreeView'
import { TreeItem } from '@mui/x-tree-view/TreeItem'

import { useActiveContent, useContentTree } from '../hooks'
import type { ContentIdentifier, ContentItem, ContentTreeItem, FilterType } from '../types'

import ContentLabel from './ContentLabel'

export type ContentTreeProps = {
  className?: string
  children?: React.ReactNode
  searchQuery?: string
  filterType?: FilterType
} & ContentIdentifier

function TreeItemContent(props: {
  content: ContentItem
  childContents?: ContentTreeItem[]
  owner: string
  repo: string
  filterType: FilterType
  onClick?: (content: ContentItem) => void
}) {
  const { content, childContents, owner, repo, filterType, onClick } = props

  return (
    <TreeItem
      key={content.sha}
      itemId={content.sha}
      label={
        <Suspense fallback={<span>Loading...</span>}>
          <ContentLabel content={content} owner={owner} repo={repo} onFileClick={onClick} />
        </Suspense>
      }
      sx={{
        padding: 0,
        '& .MuiTreeItem-content': {
          padding: 0,
        },
      }}
    >
      {childContents && renderTreeItems(childContents, owner, repo, filterType, onClick)}
    </TreeItem>
  )
}

function renderTreeItems(
  treeItems: ContentTreeItem[],
  owner: string,
  repo: string,
  filterType: FilterType,
  onClick?: (content: ContentItem) => void
) {
  return (
    <>
      {treeItems.map(({ content, children }) => (
        <TreeItemContent
          key={content.sha}
          content={content}
          childContents={children}
          owner={owner}
          repo={repo}
          filterType={filterType}
          onClick={onClick}
        />
      ))}
    </>
  )
}

// Recursively filter tree items by search query
function filterTreeBySearch(treeItems: ContentTreeItem[], searchQuery: string): ContentTreeItem[] {
  if (!searchQuery) return treeItems

  const filtered: ContentTreeItem[] = []
  for (const item of treeItems) {
    const nameMatches = item.content.name.toLowerCase().includes(searchQuery.toLowerCase())
    const filteredChildren = item.children ? filterTreeBySearch(item.children, searchQuery) : []

    // Include if name matches or has matching children
    if (nameMatches || filteredChildren.length > 0) {
      filtered.push({
        content: item.content,
        children: filteredChildren.length > 0 ? filteredChildren : item.children,
      })
    }
  }
  return filtered
}

function ContentTree(props: ContentTreeProps) {
  const { owner, repo, path, searchQuery = '', filterType = 'all' } = props
  const { contentTree, shaList } = useContentTree({ owner, repo, path })
  const { setActiveContent } = useActiveContent({ owner, repo, path })

  // Apply search filter
  const filteredTree = useMemo(
    () => filterTreeBySearch(contentTree, searchQuery),
    [contentTree, searchQuery]
  )

  // Expand all nodes when searching
  const expandedNodes = useMemo(() => {
    if (searchQuery) {
      const allShas: string[] = []
      const collectShas = (items: ContentTreeItem[]) => {
        items.forEach((item) => {
          allShas.push(item.content.sha)
          if (item.children) collectShas(item.children)
        })
      }
      collectShas(filteredTree)
      return allShas
    }
    return shaList || []
  }, [searchQuery, filteredTree, shaList])

  if (filteredTree.length === 0) {
    return (
      <Box sx={{ padding: 4, textAlign: 'center' }}>
        <Typography variant="body2" color="text.secondary">
          {searchQuery ? '  .' : ' .'}
        </Typography>
      </Box>
    )
  }

  return (
    <SimpleTreeView
      slots={{
        collapseIcon: ExpandMoreIcon,
        expandIcon: ChevronRightIcon,
      }}
      sx={{
        margin: '1rem',
        flexGrow: 1,
      }}
      defaultExpandedItems={expandedNodes}
    >
      {renderTreeItems(filteredTree, owner, repo, filterType, setActiveContent)}
    </SimpleTreeView>
  )
}

export default memo(ContentTree)
