import { useCallback, useRef } from 'react'

import { useQueryClient } from '@tanstack/react-query'

import { addLearningHistories, type LearningHistoryInsert } from '@/api/learningHistories'

import { useForegroundDuration } from './useForegroundDuration'

type ContentInfo = {
  owner: string
  repo: string
  path: string
  sha: string
}

export function useAddHistory(contentInfo: ContentInfo | null) {
  const queryClient = useQueryClient()
  const bufferRef = useRef<LearningHistoryInsert[]>([])
  const { getDuration, resetDuration } = useForegroundDuration()
  // eslint-disable-next-line react-hooks/purity
  const lastResultTimeRef = useRef(Date.now())

  const addResult = useCallback(
    (result: 'pass' | 'fail', lineNo: number) => {
      if (!contentInfo) return
      const now = Date.now()
      const duration = now - lastResultTimeRef.current
      lastResultTimeRef.current = now

      bufferRef.current.push({
        owner: contentInfo.owner,
        repo: contentInfo.repo,
        path: contentInfo.path,
        sha: contentInfo.sha,
        line_no: lineNo,
        type: result,
        duration,
      })
    },
    [contentInfo]
  )

  const completeSession = useCallback(async () => {
    if (!contentInfo) return
    const totalDuration = getDuration()

    bufferRef.current.push({
      owner: contentInfo.owner,
      repo: contentInfo.repo,
      path: contentInfo.path,
      sha: contentInfo.sha,
      line_no: -1,
      type: 'fin',
      duration: totalDuration,
    })

    const items = bufferRef.current
    bufferRef.current = []
    resetDuration()
    lastResultTimeRef.current = Date.now()

    try {
      await addLearningHistories(items)
      await queryClient.invalidateQueries({
        queryKey: ['learningHistorySummary', contentInfo.sha],
      })
    } catch (e) {
      // eslint-disable-next-line no-console
      console.error('Failed to save learning histories:', e)
    }
  }, [contentInfo, getDuration, resetDuration, queryClient])

  return { addResult, completeSession }
}
