import { memo, useCallback, useEffect, useMemo, useRef, useState } from 'react'

import { useAtomValue } from 'jotai'
// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-ignore
// import * as Diff from 'text-diff'

import HearingIcon from '@mui/icons-material/Hearing'
import VisibilityIcon from '@mui/icons-material/Visibility'
import {
  Alert,
  Box,
  Button,
  IconButton,
  LinearProgress,
  TextareaAutosize,
  Typography,
} from '@mui/material'
import { styled } from '@mui/material/styles'

import { useTypedSearchParams } from '@/hooks/useTypedSearchParams'
import { routes } from '@/routes'

import { learnConfigAtom } from '../atoms'
import { useFlashCardQuiz } from '../hooks/useFlashCardQuiz'
import { useTts } from '../lib/tts'

import {
  QuizCompleteState,
  QuizEmptyState,
  QuizProgressBar,
  QuizSuspenseWrapper,
} from './QuizLayout'
import { Question, RenderContextProvider } from './RenderContext'

export type ShortAnswerViewProps = {
  className?: string
}

function cleanUpString(s: string) {
  return s
    .toLowerCase()
    .replace(/<br\/?>/g, '')
    .replace(/[-!"'""'<>:\\.\\,?\s]/g, '')
}

function compareAnswer(source: string, target: string) {
  return cleanUpString(source) === cleanUpString(target)
}

const TextDiff = styled('div')(({ theme: { palette } }) => ({
  fontSize: '1.2rem',
  lineHeight: '1.8rem',
  wordBreak: 'break-word',
  '& span': {
    color: palette.success.main,
  },
  '& del': {
    opacity: 0,
    textDecoration: 'none',
  },
  '& ins': {
    color: palette.warning.main,
  },
  '&.showAnswer': {
    '& span': {
      color: palette.success.main,
    },
    '& del': {
      color: palette.error.main,
      opacity: 1,
    },
    '& ins': {
      color: palette.warning.main,
    },
  },
}))

function ShortAnswerViewContent(props: ShortAnswerViewProps) {
  const { className } = props
  const {
    queryParams: { owner = '', repo = '', path = '' },
  } = useTypedSearchParams(routes.flashMan.learn.shortAnswer.$config)

  const {
    content,
    token,
    flashCardItems,
    currentIndex,
    currentItem,
    total,
    passCount,
    failCount,
    progress,
    isComplete,
    recordResult,
    advance,
    restart,
  } = useFlashCardQuiz({ owner, repo, path })

  const { ttsForQuestion, ttsLang } = useAtomValue(learnConfigAtom)
  const playTts = useTts(ttsLang)

  const [answer, setAnswer] = useState('')
  const [correct, setCorrect] = useState<boolean | undefined>(undefined)
  const [showAnswer, setShowAnswer] = useState(false)
  const [checking, setChecking] = useState(false)
  const refShowAnswer = useRef(false)
  const refTextArea = useRef<HTMLTextAreaElement>(null)

  useEffect(() => {
    if (showAnswer) {
      refShowAnswer.current = true
    }
  }, [showAnswer])

  const handleResult = useCallback(
    (result: 'pass' | 'fail') => {
      recordResult(result)
      setCorrect(result === 'pass')

      setTimeout(() => {
        setShowAnswer(false)
        setCorrect(undefined)
        setAnswer('')
        setChecking(false)
        refShowAnswer.current = false
        advance()
        refTextArea.current?.focus()
      }, 1000)
    },
    [recordResult, advance]
  )

  // Handle text input
  const handleAnswer = useCallback(
    (e: React.ChangeEvent<HTMLTextAreaElement>) => {
      const value = e.target.value
      setAnswer(value)
      setShowAnswer(false)
      if (currentItem && compareAnswer(currentItem.a, value)) {
        handleResult(refShowAnswer.current ? 'fail' : 'pass')
      }
    },
    [currentItem, handleResult]
  )

  // Keyboard shortcuts
  const handleKeyDown = useCallback((e: React.KeyboardEvent<HTMLTextAreaElement>) => {
    if (e.key === 'Enter') {
      setChecking((prev) => !prev)
      e.preventDefault()
      e.stopPropagation()
    }
    if (e.key === '?') {
      setShowAnswer((prev) => !prev)
      e.preventDefault()
      e.stopPropagation()
    }
  }, [])

  // const diff = useMemo(() => new Diff(), [])
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const [diff, setDiff] = useState<any>(null)

  useEffect(() => {
    import('text-diff').then((module) => {
      // eslint-disable-next-line no-console
      console.log(module)
      setDiff(new (module.default || module)())
    })
  }, [])

  // Compute diff
  const { diffHtml, ratio } = useMemo(() => {
    if (!currentItem || !diff) return { diffHtml: '', ratio: 0 }
    const target = currentItem.a.replaceAll('<br/>', '\n').toLowerCase()
    const textDiff: [match: number, str: string][] = diff.main(target, answer.toLowerCase())
    const ratio =
      (100 * textDiff.filter(([match]) => match === 0).reduce((ret, [, s]) => ret + s.length, 0)) /
      target.length
    const diffHtml = diff.prettyHtml(textDiff)
    return { ratio, diffHtml }
  }, [currentItem, diff, answer])

  // Auto-play TTS and focus textarea on question change
  useEffect(() => {
    if (ttsForQuestion && currentItem) {
      playTts(currentItem.q)
    }
    refTextArea.current?.focus()
  }, [currentIndex, currentItem, ttsForQuestion, playTts])

  const handleRestart = useCallback(() => {
    restart()
    setAnswer('')
    setCorrect(undefined)
    setShowAnswer(false)
    setChecking(false)
    refShowAnswer.current = false
  }, [restart])

  if (!content || flashCardItems.length === 0) {
    return <QuizEmptyState />
  }

  if (isComplete) {
    return (
      <QuizCompleteState
        className={className}
        passCount={passCount}
        failCount={failCount}
        total={total}
        contentId={{ owner, repo, path }}
        onRestart={handleRestart}
      />
    )
  }

  return (
    <RenderContextProvider content={content} githubToken={token}>
      <Box
        className={className}
        sx={{
          display: 'flex',
          flexDirection: 'column',
          height: '100%',
          padding: 2,
        }}
      >
        <QuizProgressBar
          currentIndex={currentIndex}
          total={total}
          passCount={passCount}
          failCount={failCount}
          progress={progress}
        />

        {/* Question & Diff area */}
        <Box sx={{ flexGrow: 1, display: 'flex', flexDirection: 'column' }}>
          <Box
            sx={{
              flexGrow: 1,
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              justifyContent: 'center',
              p: 2,
            }}
          >
            <Question text={currentItem?.q || ''} sx={{ textAlign: 'center', mb: 2 }} />

            {correct === undefined && (showAnswer || checking) && (
              <TextDiff
                className={showAnswer ? 'showAnswer' : undefined}
                dangerouslySetInnerHTML={{ __html: diffHtml }}
              />
            )}
            {correct !== undefined && (
              <TextDiff className="showAnswer" dangerouslySetInnerHTML={{ __html: diffHtml }} />
            )}
            {correct === true && (
              <Box sx={{ mt: 1 }}>
                <Alert>.</Alert>
              </Box>
            )}
            {correct === false && (
              <Box sx={{ mt: 1 }}>
                <Alert severity="error">  .</Alert>
              </Box>
            )}
          </Box>
        </Box>

        {/* Input area */}
        <Box
          sx={{
            position: 'sticky',
            bottom: 0,
            display: 'flex',
            flexDirection: 'column',
            gap: 1,
          }}
        >
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Button onClick={() => handleResult('fail')}></Button>
            <Box sx={{ flexGrow: 1, display: 'flex', alignItems: 'center', px: 1 }}>
              <LinearProgress sx={{ width: '100%' }} variant="determinate" value={ratio} />
            </Box>
            <Button disabled={ratio < 80} onClick={() => handleResult('pass')}>
              
            </Button>
            {ttsForQuestion && (
              <IconButton
                onClick={() => {
                  if (currentItem) playTts(currentItem.q)
                }}
              >
                <HearingIcon />
              </IconButton>
            )}
            <IconButton
              color="secondary"
              disabled={showAnswer}
              onClick={() => {
                setShowAnswer(true)
                refTextArea.current?.focus()
              }}
            >
              <VisibilityIcon />
            </IconButton>
          </Box>
          <TextareaAutosize
            ref={refTextArea}
            aria-label="answer input"
            minRows={3}
            maxRows={7}
            placeholder="  "
            style={{
              fontSize: '1rem',
              padding: '.5rem',
            }}
            value={answer}
            onChange={handleAnswer}
            onKeyDown={handleKeyDown}
          />

          {/* Keyboard hints */}
          <Typography variant="caption" color="text.secondary" sx={{ textAlign: 'center' }}>
            Enter:  | ?:  
          </Typography>
        </Box>
      </Box>
    </RenderContextProvider>
  )
}

function ShortAnswerView(props: ShortAnswerViewProps) {
  return (
    <QuizSuspenseWrapper>
      <ShortAnswerViewContent {...props} />
    </QuizSuspenseWrapper>
  )
}

export default memo(ShortAnswerView)
