import { useMemo } from 'react'

import type { FlashCardContent, FlashCardItemWithSummary } from '../types'

import { useLearningHistorySummary } from './useLearningHistorySummary'

export function useFlashCardItemSummaries(
  content: FlashCardContent | null,
  sort: boolean
): FlashCardItemWithSummary[] {
  const summary = useLearningHistorySummary(content?.sha)

  return useMemo(() => {
    if (!content) return []

    const lineHistories = new Map<number, { pass: number; total: number }>()
    for (const h of summary.histories) {
      if (h.type === 'fin') continue
      const entry = lineHistories.get(h.line_no) ?? { pass: 0, total: 0 }
      entry.total++
      if (h.type === 'pass') entry.pass++
      lineHistories.set(h.line_no, entry)
    }

    const items: FlashCardItemWithSummary[] = content.flashCardItems.map((item) => {
      const entry = lineHistories.get(item.lineNo)
      const passRatio = entry && entry.total > 0 ? (entry.pass / entry.total) * 100 : 0
      return { ...item, passRatio }
    })

    if (sort) {
      items.sort((a, b) => a.passRatio - b.passRatio)
    }

    return items
  }, [content, summary.histories, sort])
}
