import { useMemo, useCallback } from 'react'

import { useSearchParams } from 'react-router-dom'
import { parseQuery, renderQuery, type WithContext, type InferQueryParams } from 'typesafe-routes'

/**
 * Type-safe hook for working with search parameters
 *
 * @example
 * ```tsx
 * function ConsolePage() {
 *   const [params, setParams] = useTypedSearchParams(routes.console);
 *
 *   // params is fully typed based on the route definition
 *   console.log(params.tab); // string | undefined
 *   console.log(params.page); // number | undefined
 *
 *   // Update params in a type-safe way
 *   setParams({ tab: 'models', page: 1 });
 * }
 * ```
 */
export function useTypedSearchParams<T extends WithContext>(route: T) {
  const [searchParams, setSearchParams] = useSearchParams()

  // Parse the current search params into the typed format
  // Need to prepend '?' if there are params
  const queryString = searchParams.toString()
  const queryParams = useMemo(
    () => parseQuery(route, queryString ? `?${queryString}` : ''),
    [route, queryString]
  )

  // Type-safe setter function
  const setQueryParams = useCallback(
    (params: Partial<InferQueryParams<T>>) => {
      const newSearch = renderQuery(route, params as InferQueryParams<T>)
      setSearchParams(newSearch)
    },
    [route, setSearchParams]
  )

  const mergeQueryParams = useCallback(
    (params: Partial<InferQueryParams<T>>) => {
      const updatedParams = { ...queryParams, ...params }
      const newSearch = renderQuery(route, updatedParams as InferQueryParams<T>)
      setSearchParams(newSearch)
    },
    [route, setSearchParams, queryParams]
  )
  return { queryParams, setQueryParams, mergeQueryParams }
}
