import { memo, Suspense, useCallback, useMemo, useRef, useState, useEffect } from 'react'

import { useAtom } from 'jotai'
import { useNavigate } from 'react-router-dom'

import CloseIcon from '@mui/icons-material/Close'
import {
  Box,
  Button,
  Checkbox,
  CircularProgress,
  FormControl,
  FormControlLabel,
  FormLabel,
  Grid,
  IconButton,
  InputLabel,
  MenuItem,
  Radio,
  RadioGroup,
  Select,
  Typography,
} from '@mui/material'

import { useStackedTitle } from '@/hooks/useStackedTitle'
import { useTypedSearchParams } from '@/hooks/useTypedSearchParams'
import { routes } from '@/routes'

import {
  learnConfigAtom,
  type Language,
  type LearnConfig,
  type QaDirection,
  type QaType,
} from '../atoms'
import { useGithubCardContextNext } from '../hooks/useFlashCardContent'
import { useFlashCardItemSummaries } from '../hooks/useFlashCardItemSummaries'
import type { ContentIdentifier } from '../types'

import ContentNavigationBar from './ContentNavigationBar'
import ContentStatus from './ContentStatus'
import HistogramSlider from './HistogramSlider'

export type LearnConfigEntryProps = {
  className?: string
  contentId: ContentIdentifier
}

function LearnConfigEntryContent(props: LearnConfigEntryProps) {
  const { className, contentId } = props
  const navigate = useNavigate()
  const [learnConfig, setLearnConfig] = useAtom(learnConfigAtom)

  const {
    shuffle,
    qaDirection,
    qaType,
    hideHint,
    soundOnLearning,
    ttsForQuestion,
    ttsForAnswer,
    ttsLang,
    passRatio,
  } = learnConfig

  const { content, contentPrev, contentNext } = useGithubCardContextNext(contentId)

  const flashCardItems = useMemo(() => {
    if (!content?.flashCardItems) return []
    return content.flashCardItems
  }, [content])

  const total = flashCardItems.length

  const itemSummaries = useFlashCardItemSummaries(content ?? null, false)
  const passRatioList = useMemo(() => itemSummaries.map((s) => s.passRatio), [itemSummaries])
  const filteredCount = useMemo(
    () => itemSummaries.filter((s) => s.passRatio < passRatio).length,
    [itemSummaries, passRatio]
  )

  const histogramContainerRef = useRef<HTMLDivElement>(null)
  const [containerWidth, setContainerWidth] = useState(300)

  useEffect(() => {
    const el = histogramContainerRef.current
    if (!el) return
    const observer = new ResizeObserver((entries) => {
      for (const entry of entries) {
        setContainerWidth(entry.contentRect.width)
      }
    })
    observer.observe(el)
    return () => observer.disconnect()
  }, [])

  const handleConfigChange = useCallback(
    (config: Partial<LearnConfig>) => {
      setLearnConfig((prev) => ({ ...prev, ...config }))
    },
    [setLearnConfig]
  )

  const handleStart = useCallback(() => {
    if (qaType === 'one-from-many') {
      navigate(routes.flashMan.learn.oneFromMany.$(contentId))
    } else {
      navigate(routes.flashMan.learn.shortAnswer.$(contentId))
    }
  }, [qaType, navigate, contentId])

  useStackedTitle(content?.name)

  // const handleClose = useCallback(() => {
  //   navigate(routes.flashMan.cards.$(contentId))
  // }, [navigate, contentId])

  // if (!content || total === 0) {
  //   return (
  //     <Box sx={{ padding: 4, textAlign: 'center' }}>
  //       <Typography variant="body2" color="text.secondary">
  //           .
  //       </Typography>
  //       <Button variant="outlined" onClick={handleClose} sx={{ mt: 2 }}>
  //         
  //       </Button>
  //     </Box>
  //   )
  // }

  return (
    <Box
      className={className}
      sx={{
        display: 'flex',
        flexDirection: 'column',
        height: '100%',
        padding: 2,
      }}
    >
      {/* Header */}
      <Box sx={{ display: 'flex' }}>
        <Box sx={{ transform: 'translate(-.5rem, -.2rem)' }}>
          <IconButton
            onClick={() => {
              navigate(routes.flashMan.cards.$(contentId))
            }}
          >
            <CloseIcon />
          </IconButton>
        </Box>
        <Box sx={{ flexGrow: 1, mb: 3 }}>
          <Typography variant="h6">{content?.name}</Typography>
          <Typography variant="body2" color="text.secondary">
             {filteredCount}/{total} 
          </Typography>
        </Box>
      </Box>

      {/* Histogram Slider */}
      <Box ref={histogramContainerRef} sx={{ mb: 2 }}>
        {containerWidth > 0 && passRatioList.length > 0 && (
          <HistogramSlider
            width={containerWidth}
            height={80}
            samples={passRatioList}
            value={passRatio}
            onChange={(v) => handleConfigChange({ passRatio: v })}
          />
        )}
      </Box>

      {/* Configuration Form */}
      <Box
        sx={{
          flexGrow: 1,
          display: 'flex',
          flexDirection: 'column',
          gap: 2,
          '& .MuiFormControl-root': {
            width: '100%',
          },
        }}
      >
        <Grid container spacing={2}>
          <Grid size={{ xs: 12 }}>
            <FormControlLabel
              control={
                <Checkbox
                  checked={shuffle}
                  onChange={() => handleConfigChange({ shuffle: !shuffle })}
                />
              }
              label=" "
            />
          </Grid>

          <Grid size={{ xs: 12 }}>
            <FormControl component="fieldset">
              <FormLabel component="legend">  </FormLabel>
              <RadioGroup
                row
                name="qa-direction"
                value={qaDirection}
                onChange={(e) => handleConfigChange({ qaDirection: e.target.value as QaDirection })}
              >
                <FormControlLabel value="forward" control={<Radio />} label=" -> " />
                <FormControlLabel value="reverse" control={<Radio />} label=" -> " />
              </RadioGroup>
            </FormControl>
          </Grid>

          <Grid size={{ xs: 12 }}>
            <FormControl component="fieldset">
              <FormLabel component="legend"> </FormLabel>
              <RadioGroup
                row
                name="qa-type"
                value={qaType}
                onChange={(e) => handleConfigChange({ qaType: e.target.value as QaType })}
              >
                <FormControlLabel value="one-from-many" control={<Radio />} label="" />
                <FormControlLabel value="short-answer" control={<Radio />} label="" />
              </RadioGroup>
            </FormControl>
          </Grid>

          <Grid size={{ xs: 12 }}>
            <FormControlLabel
              control={
                <Checkbox
                  checked={hideHint}
                  onChange={(e) => handleConfigChange({ hideHint: e.target.checked })}
                />
              }
              label=" "
            />
          </Grid>

          <Grid size={{ xs: 12 }}>
            <FormControlLabel
              control={
                <Checkbox
                  checked={soundOnLearning}
                  onChange={(e) => handleConfigChange({ soundOnLearning: e.target.checked })}
                />
              }
              label=""
            />
          </Grid>

          <Grid size={{ xs: 12 }}>
            <FormControlLabel
              control={
                <Checkbox
                  checked={ttsForQuestion}
                  onChange={(e) => handleConfigChange({ ttsForQuestion: e.target.checked })}
                />
              }
              label=" "
            />
          </Grid>

          <Grid size={{ xs: 12 }}>
            <FormControlLabel
              control={
                <Checkbox
                  checked={ttsForAnswer}
                  onChange={(e) => handleConfigChange({ ttsForAnswer: e.target.checked })}
                />
              }
              label=" "
            />
          </Grid>

          <Grid size={{ xs: 12 }}>
            <FormControl fullWidth>
              <InputLabel id="tts-lang-label">TTS </InputLabel>
              <Select
                labelId="tts-lang-label"
                value={ttsLang}
                label="TTS "
                onChange={(e) => handleConfigChange({ ttsLang: e.target.value as Language })}
              >
                <MenuItem value="ko-KR"></MenuItem>
                <MenuItem value="en-US"></MenuItem>
                <MenuItem value="ja-JP"></MenuItem>
              </Select>
            </FormControl>
          </Grid>
        </Grid>
      </Box>

      {/* Action Buttons */}
      <ContentNavigationBar
        contentId={contentId}
        content={content}
        contentPrev={contentPrev}
        contentNext={contentNext}
        centerButton={
          <Button variant="contained" size="large" onClick={handleStart} disabled={total === 0}>
             {content && <ContentStatus simple content={content} />}
          </Button>
        }
      />
    </Box>
  )
}

function LearnConfigEntry() {
  const { queryParams } = useTypedSearchParams(routes.flashMan.learn.$config)
  const { owner, repo, path } = queryParams
  return (
    <Suspense
      fallback={
        <Box
          sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '50vh' }}
        >
          <CircularProgress />
        </Box>
      }
    >
      {owner && repo && path ? (
        <LearnConfigEntryContent contentId={{ owner, repo, path }} />
      ) : (
        <>
          <Box>Invalid parameters</Box>
        </>
      )}
    </Suspense>
  )
}

export default memo(LearnConfigEntry)
