import { useMemo } from 'react'

import { useSuspenseQuery } from '@tanstack/react-query'

import { getLearningHistoriesBySha, type LearningHistoryRow } from '@/api/learningHistories'

export type LearningHistorySummary = {
  histories: LearningHistoryRow[]
  cntTries: number
  cntTotal: number
  cntPass: number
  cntFail: number
  passRate: number
  duration: number
}

export function useLearningHistorySummary(sha: string | undefined) {
  const { data: histories } = useSuspenseQuery({
    queryKey: ['learningHistorySummary', sha],
    queryFn: async () => {
      if (!sha) return []
      return getLearningHistoriesBySha(sha)
    },
    staleTime: 1000 * 60 * 5,
  })

  const summary = useMemo<LearningHistorySummary>(() => {
    const itemHistories = histories.filter((h) => h.type !== 'fin')
    const finHistories = histories.filter((h) => h.type === 'fin')
    const cntTries = finHistories.length
    const cntPass = itemHistories.filter((h) => h.type === 'pass').length
    const cntFail = itemHistories.filter((h) => h.type === 'fail').length
    const cntTotal = cntPass + cntFail
    const passRate = cntTotal > 0 ? (cntPass / cntTotal) * 100 : 0
    const duration = finHistories.reduce((sum, h) => sum + h.duration, 0)

    return { histories, cntTries, cntTotal, cntPass, cntFail, passRate, duration }
  }, [histories])

  return summary
}
